body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body {
  background: #000 url(/react-gh-pages/static/media/bg.c9dcf11d.png) no-repeat center center/cover;
  height: 100vh;
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
}
/*Home.js css*/
.home{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.home img{
  height:40vh
}

.home h1{
  color: #fff;
  font-size: 100px;
}

/*debit.js css*/
.debit{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/*credit.js css*/
.credit{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/*userProfile.js css*/
.userpro{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 15%;
}
.box{
  width: 300px;
  height: 300px;
  background-color:#004999;
  border-radius: 10px;
  text-align: center;
  padding: 20px 0;
}

.circle{
  width: 150px;
  height: 150px;
  border: 5px solid #2AA7EC;
  border-radius: 50%;
  margin: auto;
  position: relative;
}

img{
  max-width:100%;
  max-height:100%;
  border-radius:50%;
}

/*login.js css*/
.login{
  width: 262px;
  margin: 50px auto;
}
.login form{
  margin-bottom: 15px;
  background: #f7f7f7;
  padding: 30px;
}

